import { Component } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

export interface ILabel {
	print: boolean;
	number: number;
	subject: string;
	amount: number;
};

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.scss']
})
export class AppComponent {
	public isSupported: boolean = true;
	public currentFile: File;
	public currentFileContent: Document;
	public labels: Array<ILabel> = [];
	public labelOptions = {
		width: 1000,
		height: 1500,
		orientation: 'landscape',
		subject_size: 56,
		other_size: 32,
	};
	public reading: boolean = false;

	private columnHeaders = {
		number: 'Nr.',
		subject: 'Emne ID',
		filename: 'Filnavn',
	};

	constructor(
		private modalService: NgbModal,
	) {
		// Check for the various File API support.
		if (File && FileReader && FileList && window.Blob && window['chrome']) {
			// Great success! All the File APIs are supported.
		} else {
			this.isSupported = false;
		}
	}

	public onFileChange(f: any) {
		this.currentFile = (<{ target: { files: File[] } }>f).target.files[0];

		this.readFile();
	}

	public readFile() {
		if (!this.currentFile) {
			return;
		}

		// Reset vars
		this.reading = true;
		this.currentFileContent = undefined;
		this.labels = [];

		const reader = new FileReader();

		// Closure to capture the file information.
		reader.onload = (e: any) => {
			this.currentFileContent = new DOMParser().parseFromString(e.target.result, 'text/html');

			Array.from(this.currentFileContent.querySelectorAll('table')).forEach(table => {
				const columnIndexes = {};

				Array.from(table.rows).forEach((row, index) => {
					const cells = Array.from(row.cells);

					if (index === 0) {
						for (const ctmKey in this.columnHeaders) {
							const ctm = this.columnHeaders[ctmKey];
							columnIndexes[ctm] = cells.indexOf(cells.find(cell => cell.innerText === ctm));
						}
					} else {
						/**
						 * If any column indexes are missing, we skip it (essentially skipping the entire table)
						 */
						for (const ctmKey in this.columnHeaders) {
							const ctm = this.columnHeaders[ctmKey];

							if (columnIndexes[ctm] === -1) {
								return;
							}
						}

						const label = {
							print: true,
							number: parseInt(cells[columnIndexes[this.columnHeaders.number]].innerText),
							subject: cells[columnIndexes[this.columnHeaders.subject]].innerText,
							amount: null,
						};

						const amount = label.subject.match(/_([0-9]+)stk$/);

						if (amount && amount[1] && parseInt(amount[1])) {
							label.amount = parseInt(amount[1]);
						} else {
							label.amount = 1;
						}

						this.labels.push(label);
					}
				});
			});

			this.reading = false;
		};

		// Read in the image file as a data URL.
		reader.readAsText(this.currentFile);
	}

	public getLabelsWithPositions() {
		const labelsWithPos: Array<{ label: ILabel, pos: number }> = [];

		for (const label of this.labels) {
			if (label.print === false) {
				continue;
			}

			for (let i = 1; i <= label.amount; i++) {
				labelsWithPos.push({
					label: label,
					pos: i,
				})
			}
		}

		return labelsWithPos;
	}

	public print() {
		window.print();
	}

	public toggleAll() {
		const toggleTo: boolean = this.labels.length ? !this.labels[0].print : false;

		for (const label of this.labels) {
			label.print = toggleTo;
		}
	}
}
